/* sidebar */
/* custom variables*/
:root {
  --sidebar-width: 240px; }

#sidebar-main {
  position: fixed;
  height: 100%;
  top: 4rem;
  width: var(--sidebar-width);
  margin-left: calc(var(--sidebar-width) * -1);
  padding-top: 0;
  padding-bottom: 0;
  z-index: 2;
  transition: margin-left 0.15s ease-in-out; }

.sidebar-backdrop.fade {
  opacity: 0; }
  .sidebar-backdrop.fade.show {
    opacity: var(--bs-backdrop-opacity); }

.sidebar-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5); }

#content-wrapper {
  width: 100%; }

.sidebar-open #sidebar-main {
  margin-left: 0; }

@media (min-width: 768px) {
  .sidebar-backdrop {
    display: none; }
  #sidebar-main {
    position: sticky !important;
    height: 100vh !important;
    margin-left: 0; }
  .sidebar-collapsed #sidebar-main {
    margin-left: calc(var(--sidebar-width) * -1); }
  .sidebar-collapsed #content-wrapper {
    width: 100%; }
  #content-wrapper {
    width: calc(100% - var(--sidebar-width));
    transition: width 0.15s ease-in-out; } }

.sidebar {
  vertical-align: top;
