.numbered {
  position: relative;
  counter-reset: my-counter;
  list-style: none;
  padding-left: 40px !important; }

.numbered > dt:before,
.numbered > li:before {
  content: counter(my-counter);
  counter-increment: my-counter;
  position: absolute;
  left: 0;
  background-color: #8c8476;
  color: #fff;
  width: 1.5em;
  height: 1.5em;
  font-weight: normal;
  display: inline-block;
  text-align: center;
  border-radius: 50%; }

.numbered > dt[class^="col-"]:before,
.numbered > li[class^="col-"]:before {
  left: -25px; }

/* Horizontal definition lists
-------------------------------------------------- */
dt {
  font-weight: 500; }

dl.dl-horizontal {
  display: table;
  table-layout: fixed;
  /* min-width: 400px;*/
  margin-bottom: 60px; }
  dl.dl-horizontal dt,
  dl.dl-horizontal dd {
    width: auto;
    overflow: visible; }
  dl.dl-horizontal dt {
    display: table-cell;
    text-align: right;
    white-space: nowrap;
    font-weight: 500;
    padding: 7px 20px 7px 0; }
    dl.dl-horizontal dt > pre {
      margin-top: 3px; }
  dl.dl-horizontal dd {
    display: table-cell;
    padding: 7px 0; }
    dl.dl-horizontal dd.line-break {
      display: table-row; }

@media (min-width: 936px) {
  .dl-horizontal dt {
    min-width: 220px; } }

