/* background md, sm & xs devices*/
.border-dashed {
  border-style: dashed; }

.has-separator {
  display: block;
  margin-bottom: 3rem !important;
  position: relative; }
  .has-separator:before, .has-separator:after {
    position: absolute;
    left: 50%;
    height: 1px;
    content: "";
    background: #c6c2bb; }
  .has-separator:before {
    bottom: -16px;
    width: 12%;
    margin-left: -6%; }
  .has-separator:after {
    bottom: -13px;
    width: 20%;
    margin-left: -10%; }

