@import "../../../node_modules/bootstrap/scss/functions";

/* Custom PHPCG colors */
@import "theme-colors";

// Configuration
@import "../../../node_modules/bootstrap/scss/variables";
@import "../../../node_modules/bootstrap/scss/mixins";

/* sidebar */
@import "sidebar";

/* components*/
@import "components";

/* Project styles
-------------------------------------------------- */

/* barlow-condensed-500 - latin */
@font-face {
    font-family: 'Barlow Condensed';
    font-style: normal;
    font-weight: 500;
    font-display: swap;
    src: local(''),
         url('../fonts/barlow-condensed-v12-latin-500.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
         url('../fonts/barlow-condensed-v12-latin-500.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
  }

/* source-sans-pro-regular - latin */
@font-face {
    font-family: 'Source Sans Pro';
    font-style: normal;
    font-weight: 400;
    font-display: swap;
    src: local(''),
         url('../fonts/source-sans-pro-v21-latin-regular.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
         url('../fonts/source-sans-pro-v21-latin-regular.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
  }
  /* source-sans-pro-700 - latin */
  @font-face {
    font-family: 'Source Sans Pro';
    font-style: normal;
    font-weight: 700;
    font-display: swap;
    src: local(''),
         url('../fonts/source-sans-pro-v21-latin-700.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
         url('../fonts/source-sans-pro-v21-latin-700.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
  }

html {
    position: relative;
    min-height: 100%;
}

h1,
.h1 {
    font-family: 'Barlow Condensed', sans-serif;
    font-weight: 500;
}

h2,
.h2 {
    font-family: 'Barlow Condensed', sans-serif;
    font-weight: 500;
    small {
        font-size: 50%;
        &.heading-subtitle {
            position: relative;
            top:-.75rem;
        }
    }
}

h3,
.h3 {
    font-family: 'Barlow Condensed', sans-serif;
    font-weight: 500;
}

h4,
.h4,
h5,
.h5,
h6 {
    font-family: 'Barlow Condensed', sans-serif;
    font-weight: 500;
    text-transform: lowercase;
    font-variant: small-caps;
}

figure {
    width: 100%;
    position: relative;
}

img[ci-src] {
    opacity: 0;
}

.img-thumbnail {
    box-sizing: content-box;
}

p.lead {
    font-weight: 400;
    color: darken($secondary, 20%);
}

pre,
code,
code[class*="language"] {
    font-size: 0.875rem;
}

strong {
    font-weight: $font-weight-bold;
    color: $gray-800;
}

.container > section {
    margin-bottom: 200px;

    > article {
        margin-bottom: 100px;

        > section {
            margin-bottom: 100px;
        }
    }
}

section ul:not(.list-unstyled):not(.tree-list):not(.list-group):not(.pagination) {
    > li {
        position: relative;
        list-style: none;
        margin-bottom: 0.5rem;

        strong {
            color: $gray-800;
        }

        ul > li {
            &:before {
                content: " ";
                width: 4px;
                height: 4px;
                background: $gray-300;
                border-radius: 0;
                display: inline-block;
                position: absolute;
                left: -30px;
                top: 0.75em;
            }
        }

        &:before {
            content: " ";
            width: 6px;
            height: 6px;
            background: $gray-400;
            display: inline-block;
            position: absolute;
            left: -30px;
            top: 0.55em;
        }
    }
}

ol li {
    margin-bottom: 1rem;
}

.badge {
    border-radius: none !important;
}

.badge-light {
    background-color: $gray-100;
}

/* Main navbar
-------------------------------------------------- */

#website-navbar {
    z-index: 1040;

    .navbar-brand {
        min-width: 56px;
    }

    .navbar-nav .nav-link {
        display: block;
        font-weight: 500;

        &:hover,
        &:focus {
            text-decoration: none;
            color: $gray-700;
        }
    }
}

/* Cards
-------------------------------------------------- */

/* Breadcrumbs
-------------------------------------------------- */

.breadcrumb {
    .breadcrumb-item {
        font-size: 0.875rem;
        margin-bottom: 0;
    }
}

/* Numbered lists
-------------------------------------------------- */

.numbered {
    position: relative;
    counter-reset: my-counter;
    list-style: none;
    padding-left: 40px !important;
}

.numbered > dt,
.numbered > li {
    &:before {
        content: counter(my-counter);
        counter-increment: my-counter;
        position: absolute;
        left: 0;
        background-color: $gray-500;
        color: #fff;
        width: 1.5em;
        height: 1.5em;
        font-weight: normal;
        display: inline-block;
        text-align: center;
        border-radius: 50%;
    }

    &[class^="col-"]:before {
        left: -25px;
    }
}

/* Horizontal definition lists
-------------------------------------------------- */

dt {
    font-weight: 500;
}

dl.dl-horizontal {
    display: table;
    table-layout: fixed;
    /* min-width: 400px;*/
    margin-bottom: 60px;

    dt,
    dd {
        width: auto;
        overflow: visible;
    }

    dt {
        display: table-cell;
        text-align: right;
        white-space: nowrap;
        font-weight: 500;
        padding: 7px 20px 7px 0;

        > pre {
            margin-top: 3px;
        }
    }

    dd {
        display: table-cell;
        padding: 7px 0;

        &.line-break {
            display: table-row;
        }
    }
}

@media (min-width: 936px) {
    .dl-horizontal dt {
        min-width: 220px;
    }
}

/* Project Classes
-------------------------------------------------- */

#quick-access .img-overlay {
    width: auto;
    height: 258px;
}

.bootstrap-theme-thumbnail {
    width: 120px;
    height: 90px;
}

@include media-breakpoint-up(md) {
    #quick-access .img-overlay {
        height: 166.582677px;
    }
}

@include media-breakpoint-up(lg) {
    #quick-access .img-overlay {
        height: 146.267717px;
    }
}

@include media-breakpoint-up(xl) {
    #quick-access .img-overlay {
        height: 176.740157px;
    }
}

.ndd-annotator-navigator-window {
    background: transparentize($cyan, 0.5) !important;
}

/* background md, sm & xs devices*/

.border-dashed {
    border-style: dashed;
}

.has-separator {
    display: block;
    margin-bottom: 3rem !important;
    position: relative;

    &:before,
    &:after {
        position: absolute;
        left: 50%;
        height: 1px;
        content: "";
        background: $gray-300;
    }
    &:before {
        bottom: -16px;
        width: 12%;
        margin-left: -6%;
    }

    &:after {
        bottom: -13px;
        width: 20%;
        margin-left: -10%;
    }
}

i[class*="fa-"],
i[data-fa-i2svg],
i > svg {
    display: inline-block !important;
    min-width: 1.125em !important;
}

.list-group-thumbnails li {
    width: 120px;
}

.mh-100vh {
    min-height: 100vh;
}

.nowrap {
    white-space: nowrap;
}

.phpcg-screenshot {
    max-width: 705px;
    margin: 3rem auto;
    height: auto;
}

.text-light {
    color: $gray-500;
}

.underline {
    text-decoration: underline !important;
}
